.Layout-crew {
    background: url('../../assets/img/crew/background-crew-desktop.jpg');
    background-size: cover;
    min-height: 100vh;
    width: 100%;
    height: 100%;
    background-position: center;
}
@media (min-width: 750px) and (max-width: 1200px) {
    .Layout-crew {
        background: url('../../assets/img/crew/background-crew-tablet.jpg');
        background-repeat: no-repeat;
        background-size: cover;
    }
}
@media (max-width: 750px) {
    .Layout-crew {
        background: url('../../assets/img/crew/background-crew-mobile.jpg');
        background-repeat: no-repeat;
        background-size: cover;
    }
}

.activeDotCrew {
    opacity: 1;
    background-color: rgb(255, 255, 255);
}
.NotActiveDot {
    opacity: 0.17;
}